// @flow
import React, { memo, useState } from 'react';
import type { Element, AbstractComponent } from 'react';
import Table from 'components/Table';
import Loader from 'components/Loader';
import { hideIfInvalidProps } from 'utils/RenderComponent';
import _get from 'lodash/get';
import ActionLoadable from './ActionLoadable';
import { mappingCounterData } from 'helpers/Offer';
import { renderPrice } from 'helpers/Table';
import type { Offer } from 'types/Offer';
import ConfirmTransferModal from 'components/BuyerOfferStep/Step500/ConfirmTransferModal';

type Props = {
  offers: Array<Offer>,
  isLoading: boolean,
};

const checkProps = ({ offers }) => offers && offers.length;

const BuyerOfferList = (props: Props): Element<'div'> => {
  const { offers, isLoading } = props;

  const mappingOffers = offers.map(mappingCounterData);

  const [openModalConfirmTransfer, setOpenModalConfirmTransfer] = useState(
    false,
  );
  const [offerModalConfirmTransfer, setOfferModalConfirmTransfer] = useState();

  const handleOpenModalConfirmTransfer = offer => {
    setOpenModalConfirmTransfer(true);
    setOfferModalConfirmTransfer(offer);
  };

  const handleCloseModalConfirmTransfer = () => {
    setOpenModalConfirmTransfer(false);
  };

  const renderItem = ({ value }) => <span>{value || '-'}</span>;

  const renderActions = ({ row }) => (
    <ActionLoadable
      offer={_get(row, ['original'])}
      handleOpenModalConfirmTransfer={handleOpenModalConfirmTransfer}
    />
  );

  const columns = [
    {
      Header: 'ID',
      accessor: 'OfferNo',
      Cell: renderItem,
    },
    {
      Header: 'Offer Price',
      accessor: 'MappingCounter.OfferPrice',
      Cell: renderPrice,
    },
    {
      Header: 'Funding',
      accessor: 'Funding.PurchaseType',
      Cell: renderItem,
    },
    {
      Header: 'Loan Type',
      accessor: 'Funding.LoanType',
      Cell: renderItem,
    },
    {
      Header: 'Down Payment',
      accessor: 'Funding.DownPayment',
      Cell: renderPrice,
    },
    {
      Header: 'Due Diligence Contingency',
      accessor: 'MappingCounter.InspectionContingency',
      Cell: renderItem,
    },
    {
      Header: 'Sale of Home Contingency',
      accessor: 'MappingCounter.SaleOfHomeContingency',
      Cell: renderItem,
    },
    {
      Header: 'Closing Period',
      accessor: 'MappingCounter.ClosingPeriod',
      Cell: renderItem,
    },
    {
      Header: 'Furniture',
      accessor: 'MappingCounter.Furniture',
      Cell: renderItem,
    },
    {
      Header: 'Status',
      accessor: 'Step.messages.buyer',
      Cell: renderItem,
    },
    {
      Header: 'Actions',
      Cell: renderActions,
    },
  ];

  return (
    <div className="offer_block">
      <div className="offer_container">
        {isLoading && <Loader />}
        <Table columns={columns} data={mappingOffers} />
        <ConfirmTransferModal
          open={openModalConfirmTransfer}
          handleClose={handleCloseModalConfirmTransfer}
          offer={offerModalConfirmTransfer}
        />
      </div>
    </div>
  );
};

export default (hideIfInvalidProps(checkProps)(
  memo(BuyerOfferList),
): AbstractComponent<Props>);
